/******************************
General Header Styles
******************************/

#site-header {
	height: auto!important;

	#site-stripe {
		display: none;
	}

	@include breakpoint(large) {

		&.infogroup-sticky-main-nav {
			position: fixed;
			z-index: 99999;
			top: 0;
			background-color: #ffffff;
			width: 100%;
			//text-align: center;
			height: 51px;
			box-shadow: 0 1px 2px 0px $secondary-color;			
		}

		#site-stripe {		
			padding-top: 1rem;		
			background-color: $black;
			display: block;
			
			p, h1, h1, h3, h4, h5, h6 {
				color: $white!important;
			}
	
		}

	}

	#mobile-chat-call {

		position: fixed;
		top: 0;
		z-index: 99999;

		.mobileToolbarSplit {
			padding-left: 0;
			padding-right: 0;
			
			a {
				color: $white;
				display: block;
				text-align: center;
				padding-top: 6px;
				padding-bottom: 6px;
				font-family: $poppins;
				font-weight: 600;
			}
	
		}
	
		#mobileToolbarLeft {
			background: $primary-color;
		}
	
		#mobileToolbarRight {
			background: $daLightBlack;
		}

	}

}

/******************************
Logobar Styles
******************************/

#logobar {

	@include breakpoint(small) {
    	
    	#main-logo {
			max-width: 50%;
		}

		min-height: 75px;		
		margin-top: 36px;
		
  	} // end smal break point

  	@include breakpoint(medium) {

		margin-top: 0px;
    	
    	#main-logo {
			max-width: 90%;
		}
		
  	} // end medium break point

  	@include breakpoint(large) {		
    	
    	.title-bar {
			display: none!important;
		}

		#logo-left {

			height: 100%;
			min-height: 75px;
			display: flex;
			flex-direction: row;
			padding-top: 0!important;									
	
			a {
				display: flex;
				flex-direction: column;					
				justify-content:space-around;
				align-content: flex-start;
			}
	
			.title-bar {
				display: flex;
				flex-direction: column;
				justify-content: space-around;
				align-content: flex-end;
				justify-content: flex-end;
	
			}
	
		}
		
  	} // end large break point		

	#logo-left {

		padding-top: 20px;

	}
	
}

/******************************
Main Navbar Styles
******************************/

#main-navbar {
	
	padding-bottom: 16px;

	#infogroup-main-nav {
		
		&> li {					

			a {
				font-weight: 500;
				color: $black;
				font-family: $poppins;
			}
		}
	}

	@include breakpoint(medium) {    	

		
	
  	} // end medium break point

  	@include breakpoint(large) {

		padding-bottom: 0px;
		
		#infogroup-main-nav li:hover > a, #infogroup-main-nav li.current-menu-item > a {
			background-color: $daLightGrey;
			border-bottom: 3px solid $daLightBlue;		
		}			

  		#infogroup-main-nav {			

  			z-index: 999;
			height: 100%;
			min-height: 75px;							
			display: flex!important;
			flex-direction: row;
			justify-content: space-between;
			align-items: stretch;
  			
  			&> li {					

				a {
					font-size: rem-calc(13);
					height: 100%;
					padding: 0;
					display: flex;
					flex-direction: column;					
					justify-content:space-around;
					padding-left: 8px;
					padding-right: 8px;
					border-bottom: 3px solid $white;					
					
					&:hover {
						background-color: $daLightGrey;
						border-bottom: 3px solid $daLightBlue;
					}

				}

				ul {
					margin-left: 0;
					background-color: $daLightGrey;
					padding: 0;
					z-index: 999;
				}
			}

  		} 

		.is-dropdown-submenu-item a {
			color: $black;
			padding: 16px 16px!important;
			border: none!important;
			font-size: rem-calc(14)!important;

			&:hover {
				background-color: $daDarkGrey!important;
				border: none!important;
			}
		}

		.is-dropdown-submenu-item:hover > a:after {
			background-color: transparent!important;
		}

		.megamenu > ul {
			left: auto;
			width: auto;

			&> li {
				float: left!important;
				width: 220px!important;
				padding-left: 10px!important;
				padding-right: 10px!important;
				text-align: left;

				&> a {					
					font-weight: bold!important;
				}				

				&> ul {
					position: static;
					margin-left: 10px;
					display: block;
					background-color: transparent!important;				
				}				
			}
		}

  	} // end large break point

  	.register {

  		a {
			color: $black!important;
			background: $daYellow;
			border-radius: 23px;
			max-height: 46px!important;
			padding-left: 16px!important;
			padding-right: 16px!important;
			margin-top: 14px;
			border: none!important;			

			&:hover {
				background-color: $daYellowHover!important;
				border: none!important;
			}

  		}
  	}

  	.login a {
  		color: #0285fc!important;
  	}

  	.promo {
  		background: $primary-color;

  		a {
  			color: $white!important;
  		}
  	}	

	.chat {

		a {						
			position: relative;

			@extend %fa-icon;
			@extend .fas;

			@include breakpoint(large) {

				padding-left: 20px!important;

				&:before {
					position: absolute;
					font-family: 'Font Awesome 5 Free';
					top: 29px;
					left: 4px;
					font-weight: 900;
					content: fa-content($fa-var-comments) //"\f086";
				}

			}

		}

	}

	.call-now {

		a {
			
			@include breakpoint(large) {
				padding-left: 16px!important;			
				position: relative;			
				border-radius: 5px;
				max-height: 40px!important;
				padding-right: 16px!important;
				margin-top: 14px;
				border: 2px $daLightBlue solid!important;
				font-weight: 600!important;
			}

			/*
			@extend %fa-icon;
  			@extend .fas;

			&:before {
				position: absolute;
				font-family: 'Font Awesome 5 Free';
				top: 11px;
				left: 4px;
				font-weight: 900;
				content: fa-content($fa-var-phone) //"\f095";
			}
			*/

			&:hover {
				background-color: $daLightBlue!important;				
			}

		}

	}

  	#infogroup-main-nav.accordion-menu {
  		background-color: $white;
  	}

}