.page-id-2012 {

	.infogroup-sitemap {
		list-style: none;
		margin-left: 0;

		li {
			@include fa-icon;
			@extend .fa-caret-right;  			
			font-size: 1rem;
			line-height: rem-calc(20);
			display: block;
			margin-bottom: rem-calc(8);
			position: relative;

  			&:before {
				content: fa-content($fa-var-caret-right);
				font-family: 'Font Awesome 5 Free';
   				color: $primary-color;
   				position: absolute;
    			left: 0;
    			top: 0;
				display: inline-block;
				font-weight: 900;
  			}

  			a {
  				font-family: $poppins;
  				padding-left: rem-calc(8);
  				display: inline-block;
  			}
		}
	}

}